.notification {
  padding: 1rem; }
  .notification.notification_normal {
    border: 1px solid #777777;
    border-left: 5px solid #777777;
    background-color: #eee; }
  .notification.notification_esd {
    border: 1px solid #ffc107;
    border-left: 5px solid #ffc107;
    background-color: #fff3ce; }
  .notification.notification_warning {
    border: 1px solid #FF9800;
    border-left: 5px solid #FF9800;
    background-color: #ffdfb1; }
  .notification.notification_danger {
    border: 1px solid #F44336;
    border-left: 5px solid #F44336;
    background-color: #ffdddb; }
  .notification.notification_info {
    border: 1px solid #81bad4;
    border-left: 5px solid #81bad4;
    background-color: #d0eff3; }
  .notification.notification_success {
    border: 1px solid #4CAF50;
    border-left: 5px solid #4CAF50;
    background-color: #ddffde; }
  .notification h5 {
    font-weight: bold;
    margin: 0 !important; }
  .notification .rich-text {
    padding: .5rem 0;
    border-bottom: 1px solid #aaa; }
